<template>
	<div>
		<w-picker :data="pickerData" v-model="pickerValue" :rowCount='7'></w-picker>
		<w-cell title=":">{{`${pickerValue[0]},${pickerValue[1]},${pickerValue[2]===undefined?'':pickerValue[2]}`}}</w-cell>
		<w-button type='primary' @click="setPickerData()"></w-button>
		<w-button type='primary' @click="setPickerValue(['',''])">,</w-button>
	</div>
</template>

<script>

export default {
	name: 'PickerDemo',
	data () {
		return {
			msg: 'Welcome to Your Vue.js App',
			pickerData0: ['', '', '', '', '', '', '', '', '', '', '', ''],
			pickerData: [
				['', '', '', '', '', '', '', '', '1', '2', '3', '4', '5', '6', '7', '8'],
				['', '', '', '', '', '', '', '']
			],
			pickerData1: [
				['', '', '', '', '', '', ''],
				['', '', '', '', '', '', ''],
				['', '', '', '', '', '', '']
			],
			pickerValue: []
		}
	},
	created () {
	},
	methods: {
		setPickerValue (pickerValue) {
			this.pickerValue = pickerValue
		},
		setPickerData () {
			this.pickerData = this.pickerData1
			this.pickerValue = []
		}
	}
}
</script>

<style>

</style>



